.baner{
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  background-image: url("../img/baner-bg.png");
  background-repeat: no-repeat;

  &__title{
    position: relative;
    float: left;
    width: 100%;
    color: white;
    font-family: $QanelasBlack;
    font-size: 85px;
    font-weight: 900;
    margin-top: 240px;
    margin-bottom: 0;
  }

  &__sub-title{
    position: relative;
    float: left;
    width: 100%;
    color: white;
    font-family: $QanelasBlack;
    font-size: 85px;
    font-weight: 900;
    margin-top: 0;
  }

  &__description{
    position: relative;
    float: left;
    width: 100%;
    color: white;
    font-family: $QanelasBold;
    font-size: 20px;
    font-weight: 700;
    line-height: 30px;
  }

  &__button{
    position: relative;
    float: left;
    background-image: linear-gradient(to top, #9c00ff 0%, #aa43f9 100%);
    border-radius: 28px;
    box-shadow: 1.5px 2.6px 29px rgba(160, 16, 253, 0.49);
    color: #faf9f7;
    font-family: $QanelasBold;
    font-size: 20px;
    font-weight: 700;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 50px;
    padding-right: 60px;
    box-sizing: border-box;
    margin-top: 40px;

    &:after{
      position: absolute;
      display: block;
      content: "";
      width: 24px;
      height: 16px;
      background-image: url("../img/icons/button-arrow.png");
      background-repeat: no-repeat;
      top: 20px;
      right: 19px;
    }
  }

  &__text-bg{
    position: absolute;
    width: 711px;
    height: 319px;
    background-image: url("../img/bg-text.png");
    background-repeat: no-repeat;
    top: 35%;

  }
}